.block-pagination {
  width: 100%;
  text-align: center;
}

.load-more {
  display: inline-block;
  padding: 10px 16px;
  background-color: rgb(28, 28, 187);
  color: white;
  border-radius: 10px;
  border: none;

  &.is-hidden {
    display: none;
  }

  &.is-load {
    color: white;
  }
}
